import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons
import Storage from "@material-ui/icons/Storage";
import Assessment from "@material-ui/icons/Assessment";
import MultilineChart from "@material-ui/icons/MultilineChart";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import InfoArea from "components/InfoArea/InfoArea.js";

import styles from "assets/jss/material-kit-react/views/landingPageSections/productStyle.js";

const useStyles = makeStyles(styles);

export default function ProductSection() {
  const classes = useStyles();
  return (
    <div className={classes.section}>
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={4}>
            <InfoArea
              title="Data Warehouse"
              description="Your data is spreading over local excel files, ERP softwares, databases, cloud-services.
              Don't you want to store them all in a persistent, reliable and blazing-fast data center?"
              icon={Storage}
              iconColor="info"
              vertical
            />
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            <InfoArea
              title="Data Analytics"
              description="Are you tired with making same reports manually? How about a reporting system that can
              reflect your KPIs real time. A system that can be integrated into your current website, show on a central
              TV, and has proper row security level. Awesome!!"
              icon={Assessment}
              iconColor="success"
              vertical
            />
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            <InfoArea
              title="Data Science"
              description="Even more innovative? Let the machine tell you what elements on your website can create leads
               - what time of the year you need to refill stock - what topics people are commenting about..
               What more? Contact us"
              icon={MultilineChart}
              iconColor="danger"
              vertical
            />
          </GridItem>
        </GridContainer>
      </div>
    </div>
  );
}
