import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import {makeStyles} from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";

import bg from  "../../assets/img/landing-bg.jpg"

import styles from "assets/jss/material-kit-react/views/landingPage.js";

// Sections for this page
import ProductSection from "./Sections/ProductSection.js";
import TeamSection from "./Sections/TeamSection.js";
import About from './Sections/About'

const dashboardRoutes = [];

const useStyles = makeStyles(styles);

export default function LandingPage(props) {
    const classes = useStyles();
    const {...rest} = props;
    return (
        <div>
            <Header
                color="transparent"
                routes={dashboardRoutes}
                brand="PLY BI Consulting Service"
                rightLinks={<HeaderLinks/>}
                fixed
                changeColorOnScroll={{
                    height: 100,
                    color: "white",
                }}
                {...rest}
            />
            <Parallax filter image={bg}>
                <div className={classes.container}>
                    <GridContainer>
                        <GridItem xs={12} sm={12} md={6}>
                            <h1 className={classes.title}>Your Story Starts With Us.</h1>
                            <h4>
                                In the century of data, every single transaction says a word
                            </h4>
                            <h4>Ply BI wraps those words, and tell you a story</h4>
                            <br/>
                            <Button
                                color="danger"
                                size="lg"
                                href=""
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                <i className="fas fa-play"/>
                                Watch video
                            </Button>
                        </GridItem>
                    </GridContainer>
                </div>
            </Parallax>
            <div className={classNames(classes.main, classes.mainRaised)}>
                <div className={classes.container}>
                    <About/>
                    <ProductSection/>
                    <TeamSection/>
                </div>
            </div>
            <Footer/>
        </div>
    );
}
