import React from "react";
// @material-ui/core components
import {makeStyles} from "@material-ui/core/styles";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import styles from "assets/jss/material-kit-react/views/landingPageSections/productStyle.js";

const useStyles = makeStyles(styles);

export default function About() {
    const classes = useStyles();
    return (
        <div className={classes.section}>
            <GridContainer justify="center">
                <GridItem xs={12} sm={12} md={8}>
                    <h2 className={classes.title}>About us</h2>
                    <h5 className={classes.description}>
                        Established in 2020 December, with Business Intelligence as the core sector, PLY BI provides
                        enterprises with technological and domain solutions to eliminate routine, handy workload, access
                        all the data across the business, and make data-driven decisions.
                    </h5>
                    <h5 className={classes.description}>
                        During the first year of operation, PLY BI has become the primary partner with many consulting
                        companies from the US, Israel. Simultaneously, we have been working proactively in the local
                        market to increase Vietnamese enterprises' awareness and perception of digital transformation
                        and data-driven culture.
                    </h5>
                </GridItem>
                <GridItem xs={12} sm={12} md={8}>
                    <h3 className={classes.description}>
                        Our solution includes but not limited to the following technologies and domains:
                    </h3>
                </GridItem>
                <GridContainer>
                    <GridItem xs={1} sm={1} md={1}/>
                    <GridItem xs={5} sm={5} md={5}>
                        <h5 className={classes.description} align="left">Finance</h5>
                        <h5 className={classes.description} align="left">Supply chain</h5>
                        <h5 className={classes.description} align="left">Operation</h5>
                        <h5 className={classes.description} align="left">Sales/ Commerce</h5>
                        <h5 className={classes.description} align="left">Marketing</h5>
                    </GridItem>
                    <GridItem xs={5} sm={5} md={5}>
                        <h5 className={classes.description} align="left">Big data Ingestion/ Warehousing</h5>
                        <h5 className={classes.description} align="left">EDA/ Insights Extraction</h5>
                        <h5 className={classes.description} align="left">Predictive modeling</h5>
                        <h5 className={classes.description} align="left">Deep Learning (Vision)</h5>
                        <h5 className={classes.description} align="left">Data Analytics/ Visualization</h5>
                    </GridItem>
                </GridContainer>

                <GridItem xs={12} sm={12} md={8}>
                    <h2 className={classes.title}>Vision</h2>
                    <h5 className={classes.description}>
                        Help enterprises move off legacy, have all the business insights in real-time, no more subjective decisions, and cut off cost.
                    </h5>
                </GridItem>
                <GridItem xs={12} sm={12} md={8}>
                    <h2 className={classes.title}>Mission</h2>
                    <h5 className={classes.description}>
                        To provide a solution that satisfies Insightfulness, Performance, Scalability, and Stability, PLY BI focuses on three missions: <br/>
                        Inherit from state-of-the-art business models. <br/>
                        Keep up to date with the most modern technologies. <br/>
                        Build up the qualified resource of Business Consulting, Developer, Analysts/ Data Scientist. <br/>
                    </h5>
                </GridItem>
            </GridContainer>



        </div>
    );
}
