import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import {makeStyles} from "@material-ui/core/styles";

// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";
// import GoogleMapReact from 'google-map-react';


import styles from "assets/jss/material-kit-react/views/profilePage.js";
import CustomInput from "../../components/CustomInput/CustomInput";
import Button from "../../components/CustomButtons/Button";
import bg from "../../assets/img/landing-bg.jpg"

const useStyles = makeStyles(styles);

function sendMessage(name, email, message) {
    fetch("http://plybi.com:8080/message", {
        method: "post",
        body: JSON.stringify({
            name: name.value,
            email: email.value,
            message: message.value,
        }),
    }).then(function (response) {
        if (response.status === 200) {
            name.value = "";
            email.value = "";
            message.value = "";
            alert("Thank you! Our staff will contact you shortly!");
        } else alert("Error, please contact phuoc.nguyen@plybi.com");
    });
}

// const AnyReactComponent = ({ text }) => <div>{text}</div>;

export default function Contact(props) {
    const classes = useStyles();
    const {...rest} = props;

    return (
        <div>
            <Header
                color="transparent"
                brand="PlyBI Consulting Services"
                rightLinks={<HeaderLinks/>}
                fixed
                changeColorOnScroll={{
                    height: 200,
                    color: "white",
                }}
                {...rest}
            />
            <Parallax small filter image={bg}/>
            <div className={classNames(classes.main, classes.mainRaised)}>
                <div>
                    <div className={classes.container}>
                        <div className={classes.section}>
                            <GridContainer justify="center">
                                <h2 className={classes.title} align="center">Work with us</h2>
                            </GridContainer>

                            <GridContainer justify="center">

                                <GridItem cs={1} sm={1} md={1}/>
                                <GridItem cs={4} sm={4} md={4}>
                                    <h4 className={classes.description} align="left">
                                        Please leave a message, our consultants will contant you shortly
                                    </h4>
                                    <form>
                                        <GridContainer>
                                            <CustomInput
                                                labelText="Your Name"
                                                id="name"
                                                formControlProps={{
                                                    fullWidth: true,
                                                }}
                                            />

                                            <CustomInput
                                                labelText="Your Email"
                                                id="email"
                                                formControlProps={{
                                                    fullWidth: true,
                                                }}
                                            />
                                            <CustomInput
                                                labelText="Your Message"
                                                id="message"
                                                formControlProps={{
                                                    fullWidth: true,
                                                    className: classes.textArea,
                                                }}
                                                inputProps={{
                                                    multiline: true,
                                                    rows: 5,
                                                }}
                                            />
                                            <GridItem xs={12} sm={12} md={4}>
                                                <Button
                                                    color="primary"
                                                    onClick={() => {
                                                        sendMessage(
                                                            document.getElementById("name"),
                                                            document.getElementById("email"),
                                                            document.getElementById("message")
                                                        );
                                                    }}
                                                >
                                                    Send Message
                                                </Button>
                                            </GridItem>
                                        </GridContainer>
                                    </form>
                                </GridItem>
                                <GridItem cs={1} sm={1} md={1}/>
                                <GridItem cs={4} sm={4} md={4}>
                                    <h3>Address: 914 Tran Hung Dao st, ward 7, district 5<br/> Ho Chi Minh City, Vietnam
                                    </h3>
                                    <h4>Email: phuoc.nguyen@plybi.com</h4>
                                    <h4>Phone, Skype, Whatsapp: +84 909314053</h4>
                                </GridItem>
                            </GridContainer>
                        </div>
                        {/*<div style={{height: '100vh', width: '100%'}}>*/}
                        {/*    <GoogleMapReact*/}
                        {/*        bootstrapURLKeys={{key:"AIzaSyBQIbmymMbfzC6SXEGWxnBQKTFBXH2oX1s"}}*/}
                        {/*        defaultCenter={{lat: 59.95, lng: 30.33}}*/}
                        {/*        defaultZoom={11}*/}
                        {/*    >*/}
                        {/*        <AnyReactComponent*/}
                        {/*            lat={59.955413}*/}
                        {/*            lng={30.337844}*/}
                        {/*            text="My Marker"*/}
                        {/*        />*/}
                        {/*    </GoogleMapReact>*/}
                        {/*</div>*/}
                    </div>
                </div>
            </div>
            <Footer/>
        </div>
    );
}
